@use "../../scss/" as *;

.footer-top {
    padding: 50px 0;
    border-top: 4px solid rgb(238, 77, 45);
    background-color: #fbfbfb;
    color: rgba(0, 0, 0, 0.54);
    &-container {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        gap: 60px 20px;
    }
    &-list {
        text-decoration: none;
        list-style-type: none;
    }
    h2 {
        padding-bottom: 18px;
        font-weight: 700;
        color: rgba(0, 0, 0, 0.54);
        font-size: 12px;
    }
    &-item {
        padding-bottom: 10px;
        a {
            text-decoration: none;
            color: rgba(0, 0, 0, 0.54);
            font-size: 12px;
        }
        &-link {
            i {
                font-size: 16px;
                color: #4a4a4a;
                padding-right: 5px;
            }
        }
    }
    &-list-brand {
        display: grid;
        grid-template-columns: repeat(3,1fr);
        grid-gap: 5px;
    }
    &-item-brand {
        list-style-type: none;
    }
    &-download {
        display: flex;
        align-items: center;
        &-item-brand {
            width: 80px;
            height: 18px;
            margin: 10px 0 10px  15px;
        }
    }
    &-qr-image {
        width: 84px;
        height: 84px;
        border: 0.0625rem solid rgba(0,0,0,.09);
    }
    
}

