.footer-top-copyright {
    border-top: 1px solid rgba(0,0,0,.1);
    padding: 40px 0;
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    color: rgba(0,0,0,.54);
    &-left {
        min-width: 300px;
    }
    &-right {
        text-align: center;
    }
    &-right-list {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        padding-left: 40px;
    }
    &-right-item {
        list-style-type: none;
        padding-right: 5px;
    }
    &-right-link {
        border-right: 1px solid rgba(0,0,0,.54);
        padding-right: 5px;
        color: rgba(0,0,0,.54);
    }
}

.footer-end {
    background-color: #f5f5f5;
    font-size: 12px;
    padding: 42px 0;
    &-policy {
        display: flex;
        justify-content: center;
        margin-bottom: 40px;
        &-item {
            padding: 0 25px;
            border-right: 2px solid #c9c9c9;
            &:last-child {
                border: none;
            }
            a {
                color: rgba(0,0,0,.65);
            }
        }
    }

    &-content {
        // gap: 0 25px;
        display: flex;
        justify-content: center;
        &-img {
            display: flex;
            justify-content: center;
            gap: 0 25px;
            &-register1 {
                background: url(https://deo.shopeemobile.com/shopee/shopee-pcmall-live-sg/assets/4bdefde103e8aa245cd17511adde9f89.png);
